// Function to refresh tokens
const UseRefreshToken = async (apiUrl) => {
  try {
    // Call your refresh API to get new tokens
    const response = await fetch(apiUrl + "/token/refresh", {
      method: "POST",
      credentials: "include",
    });

    return response;

  } catch (error) {
    console.error("Token refresh error:", error);
    throw error;
  }
};

export { UseRefreshToken };
