import React from "react";
import "./style.scss";
import { useRef, useState, useEffect } from "react";
import { useCookies } from "react-cookie";
import { createFetchInstance } from "../api/fetchInstance";

import { UserDataMapper } from "../data/userDataMapper";
const LOGIN_URL = "/auth";


const Login = ({ children, loginSuccess, cookiesAge=86400, apiUrl="http://localhost:8080" }) => {
  const [cookies, setCookie] = useCookies(["bitUser"]);
  
  const fetchInstance = createFetchInstance(apiUrl);
  
  const userRef = useRef();
  const errRef = useRef();

  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [errMsg, setErrMsg] = useState("");

  useEffect(() => {
    userRef.current.focus();
  }, []);
  //Reset error message
  useEffect(() => {
    setErrMsg("");
  }, [email, password]);
  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const response = await fetchInstance.post(
        LOGIN_URL,
        JSON.stringify({ email, password }),
        {
          headers: { "Content-Type": "application/json" },
          withCredentials: true,
        }
      );
      const userData = UserDataMapper.map(response);
      console.warn("user data ", { ...userData });
      setCookie(
        "bitUser",
        { ...userData },
        { path: "/", maxAge: cookiesAge }
      );
      setEmail("");
      setPassword("");
      if(loginSuccess) {
        loginSuccess();
      }
    } catch (err) {
      console.error("error ", err);
      if (!err?.response) {
        setErrMsg("No server response");
      } else if (err.response?.status === 400) {
        setErrMsg("Missing Email or Password");
      } else if (err.response?.status === 4001) {
        setErrMsg("Unauthorized");
      } else {
        setErrMsg("Login failed");
      }
      errRef.current.focus();
    }
  };

  return (
    <>
      <div className="bit">
        <h2>Sign In test</h2>
        <p
          ref={errRef}
          className={errMsg ? "errmsg" : "offscreen"}
          aria-live="assertive"
        >
          {errMsg}
        </p>
        <form onSubmit={handleSubmit}>
          <label htmlFor="email">Email:</label>
          <input
            type="text"
            id="email"
            ref={userRef}
            autoComplete="off"
            onChange={(e) => setEmail(e.target.value)}
            value={email}
            required
          />
          <label htmlFor="password">Password:</label>
          <input
            type="password"
            id="password"
            onChange={(e) => setPassword(e.target.value)}
            value={password}
            required
          />
          <button type="submit" variant="primary">
            Sign In
          </button>
          Need an Account?
          
          {children}
        </form>
      </div>
    </>
  );
};

export { Login };
