import { UseRefreshToken } from "../hooks/useRefreshToken";

const createFetchInstance = (apiUrl) => {
  let refreshStatus = 204;
  // Set up the intercept logic to run once
      // Assign the refreshTokens function
      const refreshTokens = async () => {
        try {
          // Call your UseRefreshToken hook to refresh tokens
          const refreshToken = await UseRefreshToken(apiUrl);
          // Assuming the refresh API returns the new tokens in JSON format
          return refreshToken; // Placeholder for the new access token
        } catch (error) {
          console.error("Token refresh error:", error);
          throw error; // Rethrow the error to be caught by the caller
        } 
      };

  // Middleware to intercept requests and refresh tokens if needed
  const interceptRequest = async (url, options) => {
    // Stop refreshing if the refresh request status is not 204
    if(refreshStatus !== 204) {
      throw 'Could not get new token'
      return
    }
    try {
      // Make the request
      const response = await fetch(apiUrl + url, {
        ...options,
        credentials: "include",
      });

      // Check if token expired
      if (response.status === 401) {
        const newAccessToken = await refreshTokens(apiUrl);
        refreshStatus = newAccessToken.status;
        
        return interceptRequest(url, options);
      }


      if(response.ok || response?.status === 201) {
        return response.json();
      } else {
        throw new Error(response?.status);
      }

    } catch (error) {
      console.error("Fetch error:", error);
      throw error;
    }
  };

  return {
    get: async (url, options = {}) => {
      try {
        return await interceptRequest(url, options);
      } catch (error) {
        console.error("Fetch GET error:", error);
        throw error;
      }
    },
    post: async (url, data, options = {}) => {
      try {
        return await interceptRequest(url, {
          ...options,
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            ...options.headers,
          },
          body: data,
        });
      } catch (error) {
        console.error("Fetch POST error:", error);
        throw error;
      }
    },
  };
};

export { createFetchInstance };
